.Card {
    width: 300px;
    margin: 10px 30px 0 30px;
    -webkit-transition-duration: 0.3s;
    -o-transition-duration: 0.3s;
       transition-duration: 0.3s;
}

.Card a {
    text-decoration: none;
}

.Card:hover {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
}

.Media {
    height: 350px;
}

.Actions {
    display: -ms-flexbox;
    display: flex;
}



